$minHeightFooter: 300px;

footer {
  background: rgba($footerBackground,0.7);
  min-height: $minHeightFooter;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  .responsive {
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 1024px) {
      flex-direction: column;
      justify-content: flex-start; }

    div {
      margin: 40px 0;
      min-height: $minHeightFooter;
      width: 25%;
      @media screen and (max-width: 1024px) {
        width: 100%;
        border-bottom: 5px solid adjust-hue($navBackground, -15deg); } }
    a {
      font-weight: bold;
      color: white; }
    h1 {
      letter-spacing: 3px; } } }
