nav.mobile {
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 1.7em;
  color: white;
  .logo {
    height: 40%;
    justify-content: space-evenly;
    h1 {
      @media screen and (max-width: 520px) {
        font-size: 1em; } } }
  form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    select {
      width: 80%;
      height: 50px;
      margin-top: 10px;
      font-size: 1em;
      option {
        font-size: 1em;
        height: 20px;
        min-height: 0; } }

    .toggle {
      height: 40px;
      width: 100%;
      background-color: adjust-hue($navBackground, -20deg);
      display: flex;
      justify-content: center;
      align-items: center; } } }

.menu {
  font-size: 1.7em;
  width: 100%;
  position: absolute;
  z-index: 9999;
  display: none;
  .menu-item {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: $navBackground;
    color: white;
    border-bottom: 1px solid adjust-hue($navBackground, 15deg); } }
