@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hind+Vadodara:wght@300&family=Neucha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.boxLoader{display:flex;justify-content:center;align-items:center}.boxLoader{width:100%;height:90vh}.current{font-family:'PT Sans Caption', sans-serif;color:white;margin:auto;margin-top:100px;margin-bottom:100px;background:rgba(25,52,128,0.9);width:70%;max-width:1200px;border-radius:50px;display:flex;flex-direction:row;justify-content:center;align-items:flex-start}@media screen and (max-width: 1024px){.current{width:100%;flex-direction:column;margin-top:0px;margin-bottom:0px;border-radius:0}}@media screen and (max-width: 520px){.current{font-size:0.7em}}.current>div{width:50%;height:100%}@media screen and (max-width: 1024px){.current>div{width:100%}}.current .left{height:100%;font-size:2em;display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch}.current .left #mapWrapper{width:100%}.current .left #mapWrapper>div{border-radius:50px 0 0 0}@media screen and (max-width: 1024px){.current .left #mapWrapper>div{width:100%;border-radius:0 0 0 0;height:100px}}.current .left .place{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;background:rgba(32,25,128,0.9);letter-spacing:4px}.current .left .place h1{margin:0}.current .left .coord{font-weight:bold;text-align:center;letter-spacing:2px}.current .right{letter-spacing:2px;font-size:1.5em;border-left:5px solid rgba(25,43,128,0.9)}@media screen and (max-width: 1024px){.current .right{border:none}}.current .right .sys{border-radius:0 50px 0 0;background:rgba(25,43,128,0.9)}@media screen and (max-width: 1024px){.current .right .sys{border-radius:0}}.current .right .sys div{display:flex;flex-direction:row;justify-content:space-around;align-items:center}.current .right .sys div img{width:50px}.current .right .weather{display:flex;flex-direction:row;justify-content:space-around;align-items:center}.current .right .windClouds{margin:auto;padding:0 5%;background:rgba(25,43,128,0.9)}.current .right .windClouds div{width:100%;display:flex;flex-direction:row;justify-content:space-bet