.fall{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.fall .scrollWrapper{width:90%;max-width:1024px;margin-top:100px;margin-bottom:100px}@media screen and (max-width: 1024px){.fall .scrollWrapper{margin:0;width:100%;overflow-y:scroll}}.fall .scrollWrapper .chart{width:100%}@media screen and (max-width: 1024px){.fall .scrollWrapper .chart{width:1000px}}.fall .warning{border-radius:50px;border:5px solid #f50;width:50%;height:70px;background-color:red;margin-top:20px;display:flex;justify-content:center;align-items:center;font-size:1.7em}@media screen and (max-width: 1024px){.fall .warning{margin:0;border-radius:0;width:100%}}

